.header {
    display: flex;
    background-color: #fff;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
  }
*, *::before, *::after {
    box-sizing: border-box;
}
.header img {
}
.logoImage {
    display: flex;
    width: 60px;
    height: 60px;
}
.logoName {
    display: flex;
    flex-direction: column;
}
.header h6 {
    font-size: .8rem;
    padding-top: 20px;
}
.header h5, h6 {
    display: block;
    margin: 0;
    width: 120px;
    text-transform: uppercase;

    color: #3f4257;
}
.header h5 {
    font-size: 9px;
    font-weight: bold;
}
.loginBlock {
    float: right;
}
.loginBlock a {
    color: black;
}
